.main-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    height: 100vh;
    overflow: hidden;
    color: white;
    background-size: cover;
    position: relative;
}

.top-bar {
    display: flex;
    justify-content: space-between;
    width: calc(100% - 40px);
    padding: 20px;
    position: absolute;
    top: 0;
    left: 0;
}

.top-bar-left, .top-bar-right {
    display: flex;
    align-items: center;
}

.top-bar-right {
    opacity: 0;
    transition: opacity 1s ease-in;
}

.dooji-os, .time-container {
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 25px;
    padding: 5px 10px;
    height: 40px;
}

.os-text, .time-text {
    margin-right: 10px;
    font-size: 18px;
}

.github-logo-container {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
}

.github-logo {
    width: 20px;
    height: 20px;
}

.time-text {
    text-align: center;
    margin-left: 10px;
}

.content-container {
    position: absolute;
    top: 80px;
    bottom: 58px;
    left: 0;
    right: 0;
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: auto;
    opacity: 0;
    transition: opacity 1s ease-in;
}

.content-container::-webkit-scrollbar {
    width: 12px;
}

.content-container::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.2); 
}

.content-container::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 10px;
    border: 3px solid rgba(0, 0, 0, 0.2);
}

.content-container::-webkit-scrollbar-thumb:hover {
    background-color: rgba(0, 0, 0, 0.8);
}

.content-container.visible {
    opacity: 1;
}

.top-bar-right.visible {
    opacity: 1;
}

.copy-text {
    color: #0c9;
    cursor: pointer;
    font-weight: bold;
    margin-left: 5px;
}

.copy-text:hover {
    color: #0f0;
}

.notification {
    position: fixed;
    bottom: -50px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    transition: bottom 0.5s ease;
    font-size: 1rem;
}

.notification.show {
    bottom: 20px;
}

.mainFooter {
    width: 100%;
    position: absolute;
    bottom: 0;
    text-align: center;
    padding: 20px 0;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: opacity 1s ease-in;
}

.mainFooter.visible {
    opacity: 1;
}

.mainFooter p {
    margin: 0;
    font-size: 16px;
    color: white;
}