.flyout-details-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    backdrop-filter: blur(5px);
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 1000;
}

.flyout-details-overlay.fade-in {
    opacity: 1;
}

.flyout-details-overlay.fade-out {
    opacity: 0;
}

.flyout-details {
    background-color: rgba(0, 0, 0, 0.85);
    padding: 20px;
    border-radius: 15px;
    width: 60vw;
    max-width: 900px;
    max-height: 80vh;
    overflow-y: auto;
    color: white;
    position: relative;
    transform: translateY(0);
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.flyout-details-overlay {
    display: flex;
    justify-content: center;
    align-items: center;
}

.flyout-details-close {
    position: absolute;
    top: 15px;
    right: 15px;
    background: none;
    color: white;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
}

.flyout-details-header {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.flyout-details-icon {
    width: 50px;
    height: 50px;
    margin-right: 10px;
}

.flyout-details-title {
    font-size: 1.8rem;
}

.flyout-details-content {
    margin-bottom: 20px;
    padding: 0 20px;
    white-space: normal;
    word-wrap: break-word;
    overflow-wrap: break-word;
    word-break: break-word;
    overflow-y: auto;
    overflow-x: hidden;
    max-height: 60vh;
}

.flyout-details-content::-webkit-scrollbar {
    width: 8px;
}

.flyout-details-content::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
}

.flyout-details-content::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 10px;
    border: 2px solid rgba(255, 255, 255, 0.1);
}

.flyout-details-buttons {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
}

.flyout-details-button {
    padding: 10px 20px;
    margin-left: 10px;
    border-radius: 25px;
    text-decoration: none;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.button-icon {
    margin-right: 5px;
    width: 20px;
    height: 20px;
}

.flyout-details-close {
    position: absolute;
    top: 15px;
    right: 15px;
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
    transition: transform 0.2s ease, opacity 0.2s ease;
}

.flyout-details-close .close-icon {
    width: 24px;
    height: 24px;
    transition: transform 0.2s ease;
}

.flyout-details-close:hover .close-icon {
    transform: scale(0.9);
}

.flyout-details-close:active .close-icon {
    transform: scale(0.8);
}

.flyout-details-close .close-icon:hover {
    opacity: 0.9;
}