.flyout {
    margin: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 15px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    overflow: hidden;
    transition: transform 0.35s ease, box-shadow 0.25s ease;
}

.flyout-content {
    transition: transform 0.35s ease, box-shadow 0.25s ease;
}

.flyout:hover .flyout-content {
    transform: scale(1.02);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

.flyout:hover .iframe-container {
    transform: scale(1.02);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

.flyout-header {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.flyout-icon {
    margin-right: 10px;
}

.flyout-icon.crop {
    border-radius: 15px;
    object-fit: cover;
}

.flyout-title {
    font-size: 1.5rem;
    color: white;
}

.copy-text {
    color: #0c9;
    cursor: pointer;
    font-weight: bold;
    margin-left: 5px;
}

.copy-text:hover {
    color: #0f0;
}

.flyout-description {
    color: white;
    flex-grow: 1;
}

.button-container {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.flyout-button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 20px;
    margin: 0 5px;
    border-radius: 25px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    text-decoration: none;
    font-size: 1rem;
    border: none;
}

.button-icon {
    margin-right: 5px;
    width: 20px;
    height: 20px;
}

.iframe-container {
    position: relative;
    z-index: 1;
    transition: transform 0.35s ease, box-shadow 0.25s ease;
}

.notification {
    position: fixed;
    bottom: -50px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    transition: bottom 0.5s ease;
    font-size: 1rem;
}

.notification.show {
    bottom: 20px;
}
