.login-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    overflow: hidden;
    color: white;
    background-size: cover;
    position: relative;
}

.top-bar {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 20px;
    position: absolute;
    top: 0;
    left: 0;
}

.top-bar-left {
    display: flex;
    align-items: center;
}

.dooji-os {
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 25px;
    padding: 5px 10px;
}

.os-text {
    margin-right: 10px;
}

.github-logo-container {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
}

.github-logo {
    width: 20px;
    height: 20px;
}

.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    opacity: 0;
    transition: opacity 1s ease-in;
}

.login-container.visible {
    opacity: 1;
}

.logo {
    width: 100px;
    height: 100px;
    margin-bottom: 20px;
    border-radius: 15px;
    background-color: rgba(0, 0, 0, 0.5);
}

.form {
    margin-top: 20px;
}

.input-container {
    display: flex;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 25px;
    padding: 10px;
    transition: all 0.3s ease;
    opacity: 1;
    transition: opacity 1s ease-in-out;
}

.input-container.hidden {
    opacity: 0;
}

.welcome-container {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 25px;
    padding: 10px;
    margin-top: 20px;
    height: 35px;
    opacity: 0;
    transition: opacity 1s ease-in;
}

.welcome-container.visible {
    opacity: 1;
}

.input {
    padding: 10px;
    border-radius: 25px;
    border: none;
    background: none;
    color: white;
    outline: none;
}

.input::placeholder {
    color: #ccc;
}

.button {
    padding: 10px 20px;
    border-radius: 25px;
    background-color: #0a243e;
    color: white;
    border: none;
    cursor: pointer;
    margin-left: 10px;
}

.welcome-text {
    color: white;
    font-size: 18px;
    text-align: center;
}

.shake {
    animation: shake 0.5s;
}

@keyframes shake {
    0%, 100% {
        transform: translateX(0);
    }
    25% {
        transform: translateX(-5px);
    }
    50% {
        transform: translateX(5px);
    }
    75% {
        transform: translateX(-5px);
    }
}

.footer {
    position: absolute;
    bottom: 20px;
    color: white;
    text-align: center;
    width: 100%;
    transition: opacity 1s ease-in-out;
}

.footer.hidden {
    opacity: 0;
}
