.main-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    height: 100vh;
    overflow: hidden;
    color: white;
    background-size: cover;
    position: relative;
}

.top-bar {
    display: flex;
    justify-content: space-between;
    width: calc(100% - 40px);
    padding: 20px;
    position: absolute;
    top: 0;
    left: 0;
}

.top-bar-left, .top-bar-right {
    display: flex;
    align-items: center;
}

.top-bar-right {
    opacity: 0;
    transition: opacity 1s ease-in;
}

.top-bar-right.visible {
    opacity: 1;
}

.dooji-os, .time-container {
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 25px;
    padding: 5px 10px;
    height: 40px;
}

.os-text, .time-text {
    margin-right: 10px;
    font-size: 18px;
}

.github-logo-container {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
}

.github-logo {
    width: 20px;
    height: 20px;
}

.time-text {
    text-align: center;
    margin-left: 10px;
}

.content-wrapper {
    opacity: 0;
    transition: opacity 1s ease-in;
}

.content-wrapper.visible {
    opacity: 1;
}
